.homepage__root {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: white;
  font-family: 'Raleway', sans-serif;
  color: rgba(50, 59, 67, 0.9);
}

/* Header -------------------- */

.homepage__headerRoot {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  display: flex;
  box-shadow: 0 0 40px 0 rgba(82, 63, 105, 0.1);
  padding-left: 5%;
  padding-right: 5%;
}

.homepage__navbarPrimary {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.homepage__navbarLogo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.homepage__navbarTitle {
  font-size: 1.5rem;
  font-weight: 600;
  padding-left: 20px;
}

.homepage__navbarSecondary {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin-left: auto;
  font-size: 1rem;
  font-weight: 600;
}

/* Body -------------------- */

.homepage__bodyRoot {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  padding-left: 5%;
  padding-right: 5%;
}

.homepage__bodyColumn {
  display: flex;
  justify-content: space-between;
  //height: calc(100vh - 80px);
}

.homepage__bodyTitle {
  display: flex;
  width: 100%;
  font-size: 3rem;
  font-weight: 700;
  margin-top: 50px;
}

.homepage__bodyParagraph {
  display: flex;
  justify-content: space-evenly;
  max-width: 40%;
  margin-top: 50px;
  padding-right: 5%;
  font-weight: 300;
  font-size: 1.5rem;
}

.homepage__bodyVideo {
  //max-width: 50%;
  //margin-left: auto;
  height: auto;
  width: 50%;
}

.homepage__bodyHeader {
  display: flex;
  align-items: center;
}

.homepage__bodyHeader > span {
  margin-left: 20px;
  font-size: 1.25rem;
  font-weight: 500;
}

.homepage__videoWrapper {
  margin-top: 20px;
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  box-shadow: 0 7px 14px rgba(60, 66, 87, 0.12), 0 3px 6px rgba(0, 0, 0, 0.12);
}

.homepage__videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

/* Footer -------------------- */

.homepage__footerRoot {
  display: flex;
  align-items: center;
  height: 80px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding-left: 5%;
  padding-right: 5%;
}

.homepage__footerPrimary {
  font-size: 1.25rem;
  font-weight: 400;
  color: #4AA7BB;
}

.homepage__footerSecondary {
  margin-left: auto;
}

.homepage__footerSecondary > a > svg {
  height: 30px;
  margin-left: 20px;
}

